<template>
    <div class="container">
        <g-breadcrumb
            :breadcrumbs="[
                { to: '/', name: 'トップ' },
                { to: null, name: '無料新規登録' }
            ]"
        />
        <g-page-title
            title="無料新規登録"
            icon="sign-in-alt"
            class="w-full mb-4"
        />
        <div class="flex justify-center items-center lg:p-6">
            <div class="w-full max-w-2xl">
                <div class="flex items-center">
                    <img
                        src="@/assets/contrat.png"
                        alt="ログイン"
                        class="hidden mr-4 lg:block"
                    />
                    <p class="text-sm">
                        原料・受託バンクに無料登録すると、御社の商品・サービスに興味がある人とのマッチングが生まれます。<br />
                        商品・サービスの情報を掲載することで、原料を買いたいバイヤー、受託製造企業を探すメーカーの方などに、あなたの会社を見つけてもらうことができるようになります。
                    </p>
                </div>

                <div v-if="completed" class="my-8">
                    <t-card
                        base-class="bg-gray-100 rounded-lg border shadow-md z-10 lg:p-6"
                    >
                        <h2
                            class="font-semibold text-center text-2xl mb-10 lg:text-3xl"
                        >
                            本登録メールを送信しました
                        </h2>
                        <p>
                            原料・受託バンクに興味をお持ち頂き有難う御座います。<br />
                            本登録は完了していません。送信されたメールのURLから本登録を完了させてください。
                        </p>
                        <t-button
                            type="button"
                            to="/"
                            variant="primary"
                            size="lg"
                            class="mt-10 w-full"
                        >
                            トップページに戻る
                        </t-button>
                    </t-card>
                </div>
                <div v-else class="my-8">
                    <form @submit.prevent="onSubmit">
                        <t-input-group
                            label="メールアドレス"
                            :status="formState('email').status"
                            :feedback="formState('email').message"
                        >
                            <t-input
                                type="email"
                                v-model="form.email"
                                label="メールアドレス"
                                autofocus
                                autocapitalize="off"
                            />
                        </t-input-group>
                        <t-input-group
                            label="パスワード"
                            class="mt-6"
                            :status="formState('password').status"
                            :feedback="formState('password').message"
                        >
                            <g-password-input
                                v-model="form.password"
                                label="パスワード"
                                icon="eye"
                            />
                        </t-input-group>
                        <t-input-group
                            label="パスワード確認"
                            class="mt-6"
                            :status="formState('password_confirmation').status"
                            :feedback="
                                formState('password_confirmation').message
                            "
                        >
                            <g-password-input
                                v-model="form.password_confirmation"
                                label="パスワード確認"
                                icon="eye"
                            />
                        </t-input-group>
                        <t-button
                            type="submit"
                            variant="primary"
                            size="lg"
                            class="mt-10 w-full"
                        >
                            会員登録する
                        </t-button>
                    </form>
                    <div class="flex flex-col items-center">
                        <div class="w-1/3 border-b-4 mb-4" />
                        <router-link
                            to="/login"
                            class="text-blue-600 hover:underline"
                        >
                            <font-awesome-icon
                                :icon="['fas', 'caret-square-right']"
                                class="text-green-800"
                            />
                            既にアカウントをお持ちの方はこちら
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from "vuex";
import GPageTitle from "@/components/GPageTitle";
import GBreadcrumb from "@/components/GBreadcrumb";
import GPasswordInput from "@/elements/GPasswordInput";

export default {
    components: {
        GPageTitle,
        GBreadcrumb,
        GPasswordInput
    },
    data() {
        return {
            form: {
                email: null,
                password: null,
                password_confirmation: null
            },
            errors: null,
            completed: false
        };
    },
    methods: {
        formState(name) {
            return this.errors &&
                this.errors[name] &&
                0 < this.errors[name].length
                ? { status: "error", message: this.errors[name][0] }
                : { status: null, message: null };
        },
        onSubmit() {
            this.register(this.form)
                .then(() => {
                    this.completed = true;
                })
                .catch(err => {
                    const response = err.response;
                    const errors = response.data.errors;
                    if (errors) {
                        this.errors = errors;
                    }
                });
        },
        ...mapActions({
            register: "auth/register"
        })
    }
};
</script>
