<template>
    <div class="container">
        <g-breadcrumb
            :breadcrumbs="[
                { to: '/', name: 'トップ' },
                { to: null, name: 'パスワード忘れた方' }
            ]"
        />
        <g-page-title
            title="パスワード忘れた方"
            icon="sign-in-alt"
            class="w-full mb-4"
        />
        <div class="flex justify-center items-center lg:p-6">
            <div class="w-full max-w-2xl">
                <div v-if="completed" class="my-8">
                    <t-card
                        base-class="bg-gray-100 rounded-lg border shadow-md p-6 z-10"
                    >
                        <h2
                            class="font-semibold text-center text-2xl mb-10 lg:text-3xl"
                        >
                            パスワード再設定メールを送信しました
                        </h2>
                        <p>
                            メール内のリンクをクリックし、パスワード再設定を行ってください。
                        </p>
                        <t-button
                            type="button"
                            to="/"
                            variant="primary"
                            size="lg"
                            class="mt-10 w-full"
                        >
                            トップページに戻る
                        </t-button>
                    </t-card>
                </div>
                <div v-else>
                    <form class="my-8" @submit.prevent="onSubmit">
                        <p class="text-center mb-10">
                            <font-awesome-icon
                                :icon="['fas', 'exclamation-triangle']"
                                class="text-yellow-600 mr-2"
                                size="lg"
                            />
                            パスワードを忘れた方は、登録したメールアドレスを入力してください。
                        </p>
                        <t-input-group
                            label="メールアドレス"
                            :status="formState('email').status"
                            :feedback="formState('email').message"
                        >
                            <t-input
                                type="email"
                                v-model="form.email"
                                label="メールアドレス"
                                autofocus
                                autocapitalize="off"
                            />
                        </t-input-group>
                        <t-button
                            type="submit"
                            variant="primary"
                            size="lg"
                            class="mt-10 w-full"
                        >
                            メールを送信する
                        </t-button>
                    </form>
                    <div class="flex flex-col items-center">
                        <div class="w-1/3 border-b-4 mb-4" />
                        <router-link
                            to="/login"
                            class="text-blue-600 hover:underline"
                        >
                            <font-awesome-icon
                                :icon="['fas', 'caret-square-right']"
                                class="text-green-800"
                            />
                            ログイン画面に戻る
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import GPageTitle from "@/components/GPageTitle";
import GBreadcrumb from "@/components/GBreadcrumb";

export default {
    components: {
        GPageTitle,
        GBreadcrumb
    },
    data() {
        return {
            form: {
                email: null
            },
            errors: null,
            completed: false
        };
    },
    methods: {
        formState(name) {
            return this.errors &&
                this.errors[name] &&
                0 < this.errors[name].length
                ? { status: "error", message: this.errors[name][0] }
                : { status: null, message: null };
        },
        onSubmit() {}
    }
};
</script>
