import Vue from "vue";
import VueRouter from "vue-router";
import Register from "../views/Register.vue";
import Login from "../views/Login.vue";
import ForgotPassword from "../views/ForgotPassword.vue";
import Home from "../views/Home.vue";
import store from "../store";

Vue.use(VueRouter);

const routes = [
    {
        path: "/login",
        name: "login",
        component: Login,
        meta: { layout: "home", requiresGuest: true }
    },
    {
        path: "/register",
        name: "register",
        component: Register,
        meta: { layout: "home", requiresGuest: true }
    },
    {
        path: "/verify",
        name: "verify",
        meta: { layout: "home", requiresGuest: true },
        beforeEnter: (to, from, next) => {
            store.dispatch("showOverlayLoading");
            store
                .dispatch("auth/verify", to.query)
                .then(() => {
                    store.dispatch("hideOverlayLoading");
                    next("/mypage/register");
                })
                .catch(error => {
                    const response = error.response;
                    to.matched[0].meta.layout = "error";
                    to.matched[0].meta.error = response.status;
                    store.dispatch("hideOverlayLoading");
                    next();
                });
        }
    },
    {
        path: "/forgot",
        name: "forgot",
        component: ForgotPassword,
        meta: { layout: "home", requiresGuest: true }
    },
    {
        path: "/",
        name: "home",
        component: Home,
        meta: { layout: "home" }
    },

    {
        path: "/mypage/register",
        name: "mypage-register",
        component: () =>
            import(
                /* webpackChunkName: "mypage-register" */ "../views/Mypage/Register.vue"
            ),
        meta: { layout: "home", requiresAuth: true }
    },
    {
        path: "/mypage",
        name: "mypage",
        component: () =>
            import(
                /* webpackChunkName: "mypage" */ "../views/Mypage/Overview.vue"
            ),
        meta: { layout: "home", requiresMypage: true }
    },
    {
        path: "/mypage/profile",
        name: "mypage-profile",
        components: {
            default: () =>
                import(
                    /* webpackChunkName: "mypage" */ "../views/Mypage/Overview.vue"
                ),
            modal: () =>
                import(
                    /* webpackChunkName: "mypage-profile" */ "../views/Mypage/Profile.vue"
                )
        },
        meta: { layout: "home", requiresMypage: true }
    },
    {
        path: "/mypage/change-mail",
        name: "mypage-change-mail",
        components: {
            default: () =>
                import(
                    /* webpackChunkName: "mypage" */ "../views/Mypage/Overview.vue"
                ),
            modal: () =>
                import(
                    /* webpackChunkName: "mypage-change-mail" */ "../views/Mypage/ChangeMail.vue"
                )
        },
        meta: { layout: "home", requiresMypage: true }
    },
    {
        path: "/mypage/change-password",
        name: "mypage-change-password",
        components: {
            default: () =>
                import(
                    /* webpackChunkName: "mypage" */ "../views/Mypage/Overview.vue"
                ),
            modal: () =>
                import(
                    /* webpackChunkName: "mypage-change-mail" */ "../views/Mypage/ChangePassword.vue"
                )
        },
        meta: { layout: "home", requiresMypage: true }
    },
    {
        path: "/mypage/leave",
        name: "mypage-leave",
        components: {
            default: () =>
                import(
                    /* webpackChunkName: "mypage" */ "../views/Mypage/Overview.vue"
                ),
            modal: () =>
                import(
                    /* webpackChunkName: "mypage-profile" */ "../views/Mypage/Leave.vue"
                )
        },
        meta: { layout: "home", requiresMypage: true }
    },

    {
        path: "*",
        meta: { layout: "error", error: 404 }
    }
];

const router = new VueRouter({
    mode: "history",
    base: "/",
    routes,
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        } else {
            return { x: 0, y: 0 };
        }
    }
});

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresMypage)) {
        if (!store.getters["auth/loggedIn"]) {
            next({ path: "/login" });
        } else if (!store.getters["auth/initialized"]) {
            next({ path: "/mypage/register" });
        } else {
            next();
        }
    } else if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!store.getters["auth/loggedIn"]) {
            next({ path: "/login" });
        } else {
            next();
        }
    } else if (to.matched.some(record => record.meta.requiresGuest)) {
        if (store.getters["auth/loggedIn"]) {
            next({ path: "/" });
        } else {
            next();
        }
    } else {
        next();
    }
});

export default router;
