const formValidator = {
    errors: null,
    state(name) {
        return this.errors && this.errors[name] && 0 < this.errors[name].length
            ? { status: "error", message: this.errors[name][0] }
            : { status: null, message: null };
    },
    setErrors(errors) {
        this.errors = errors;
    }
};

export default formValidator;
