const getters = {
    const: state => {
        return state.const;
    },
    businessCategoryOption: state => {
        let option = [];
        if (state.const && state.const.businessCategories) {
            option.push(
                ...state.const.businessCategories.map(v => ({
                    text: v.name,
                    value: v.id
                }))
            );
        }
        return option;
    }
};

export default getters;
